/** @private
* @param {Object} e
*/
Grid.prototype._onColumnAdded = function(e) {
	var colDef = e.context[COL_DEF];
	delete e.context[COL_DEF];
	var idx = e.colIndex;

	var colData = this._grid.getColumnData(idx);
	if(!colData) { // Save column inside grid
		colData = this._grid.setColumnData(idx, {});
	}
	colData[COL_DEF] = colDef;

	// binding
	this._grid.setColumnDataBindingHandler(idx, colDef.getRenderer());
	// width
	if(colDef.getWidth() >= 0) {
		this._grid.setColumnWidth(idx, colDef.getWidth(), false);
	} else {
		this._grid.setColumnWidth(idx, 1, true);
	}
	// minWidth
	if(colDef.getMinWidth() >= 0) {
		this._grid.setMinimumColumnWidth(idx, colDef.getMinWidth());
	}
	// hidden
	if(colDef.isHidden()) {
		this._grid.hideColumn(idx, true);
	}
	var textAlign = colDef.getTextAlign();
	if(textAlign) {
		this._grid.setColumnAlignment(idx, textAlign);
	}

	// className
	var classes = colDef.getClasses();
	for(var i = 0; i < classes.length; i++) {
		this._grid.enableColumnClass(idx, classes[i]);
	}
	// notRealTimeField
	if(!colDef.isRealTimeField()) {
		this._dc.addStaticFields([colDef.getField()]);
	}
	this._grid.setDataColumnName(idx, ROW_DEF); // This make ColumnDefinition renderer work

	if(this._subs) {
		this._subs["addFields"](ColumnDefinition.getRealTimeFields([], colDef));
	}
	this._fields.addReferences(colDef.getAllFields(), colDef.getId());
	this._requestSnapshotRics();
	this._snapshot.addField(this._fields.getSession().newEntries);
	this._fields.newSession();
};

/** @public
* @param {Grid~ColumnReference} colRef
*/
