	this._fields.addReferences(fieldRef, referrer || "grid");
	var newFields = this._fields.getSession().newEntries;

	if(newFields.length) {
		this._fields.newSession();
		if(this._subs) {
			this._subs["addFields"](newFields);
		}
		this._requestSnapshotRics();
		this._snapshot.addFields(newFields);
	}
	return newFields.length ? true : false;
