import {Ext} from '../../tr-grid-util/es6/Ext.js';
import { GridPlugin } from '../../tr-grid-util/es6/GridPlugin.js';
import { extendObject } from '../../tr-grid-util/es6/Util.js';
import {CellPainter} from '../../tr-grid-util/es6/CellPainter.js';
import {FilterBuilder} from '../../tr-grid-util/es6/FilterBuilder.js';

/** @typedef {Object} ConditionalColoringPlugin~ColumnOptions
* @description Extension column options that can be specified on each individual grid's column option:
* @property {Array.<ConditionalColoringPlugin~Condition>=} conditions=null List of condition options
* @property {(string|boolean)=} colorText=null A shorthand for specifying default condition based on the field.
* @property {(string|boolean)=} tickColor=null Alias of colorText.
* @property {ConditionalColoringPlugin~Blinking=} blinking=null Blink Options. If specified, the cell will be blinked on data change.
* @property {string=} field
*/

/** @typedef {Object} ConditionalColoringPlugin~Condition
* @description Available options describing `condition` object
* @property {(string|Function)=} expression Expression could be `[FIELD_1] > 0`
* @property {string=} backgroundColor="" CSS color (e.g. #ffffff, black)
* @property {string=} color CSS="" color (e.g. #000000, white)
*/

/** @typedef {(string|boolean|Object)} ConditionalColoringPlugin~Blinking
* @description Available options describing `blinking` object. Additionally if specify blinking as string of field, blinking will base on the field. <br>
* If specified true, field from column definition will be used.
* @property {boolean=} border=false If enabled, border will be blinked instead of background
* @property {string=} field If not specified, field from column definition will be used
* @property {string=} up CSS color (e.g. #00ff00, green)
* @property {string=} down CSS color (e.g. #ff0000, red)
* @property {string=} level CSS color (e.g. #33333, grey)
*/

/** @constructor
* @extends {GridPlugin}
*/
var ConditionalColoringPlugin = function () {
	this._onSectionBinding = this._onSectionBinding.bind(this);
	this._onColumnAdded = this._onColumnAdded.bind(this);
	this._onColumnRemoved = this._onColumnRemoved.bind(this);

	this._hosts = [];
};
Ext.inherits(ConditionalColoringPlugin, GridPlugin);
/** @type {boolean}
* @private
*/
ConditionalColoringPlugin.prototype._blinkingEnabled = false; // WORKAROUND: To improve performance


/** @override
* @return {string}
*/
ConditionalColoringPlugin.prototype.getName = function () {
	return "ConditionalColoringPlugin"; // Readonly
};
/** @public
* @param {tr.Grid} host
* @param {Object=} options
*/
ConditionalColoringPlugin.prototype.initialize = function (host, options) {
	if(this._hosts.indexOf(host) >= 0) { return; }

	this._hosts.push(host);

	this.config(options);

	host.listen("postSectionDataBinding", this._onSectionBinding);
	host.listen("columnAdded", this._onColumnAdded);
	host.listen("columnRemoved", this._onColumnRemoved);
	// In case of lazy loading
	// DO something
};
/** @public
* @param {tr.Grid} host
*/
ConditionalColoringPlugin.prototype.unload = function (host) {
	var at = this._hosts.indexOf(host);
	if(at < 0) { return; }
	this._hosts.splice(at, 1);

	host.unlisten("postSectionDataBinding", this._onSectionBinding);
	host.unlisten("columnAdded", this._onColumnAdded);
	host.unlisten("columnRemoved", this._onColumnRemoved);

	this._dispose();
};

/** @public
* @param {Object=} options
*/
ConditionalColoringPlugin.prototype.config = function (options) {
	if(!options) { return; }

	var columns = options["columns"];
	if(!columns) { return; }

	var len = columns.length;
	for(var i = 0; i < len; ++i) {
		this.setColumnColoring(i, columns[i]);
	}
};

/** @public
* @param {Object=} gridOptions
* @return {!Object}
*/
ConditionalColoringPlugin.prototype.getConfigObject = function (gridOptions) {
	var obj = gridOptions || {};

	var columns = obj.columns;
	if (!columns) {
		columns = obj.columns = [];
	}

	var len = this.getColumnCount();
	var host = this._hosts[0];
	for (var i = 0; i < len; ++i) {
		var column = columns[i];
		if (!column) {
			column = columns[i] = {};
		}

		var conditions = this._getColumnOption(i, 'conditions', host);
		if (Array.isArray(conditions) && conditions.length > 0) {
			column.conditions = [];

			var count = conditions.length;
			for (var colIndex = 0; colIndex < count; colIndex++) {
				var exCondition = {};
				extendObject(exCondition, conditions[colIndex], [
					'expression',
					'backgroundColor',
					'color'
				]);

				column.conditions.push(exCondition);
			}
		}

		var colorText = this._getColumnOption(i, 'colorText', host);
		if (colorText) {
			column.colorText = colorText;
		}

		// alias with colorText
		var tickColor = this._getColumnOption(i, 'tickColor', host);
		if (tickColor) {
			column.tickColor = tickColor;
		}

		var blinking = this._getColumnOption(i, 'blinking', host);
		if (blinking) {
			column.blinking = blinking;
		}

		var field = this._getField(i);
		if (field) {
			column.field = field;
		}
	}

	return obj;
};

/** @public
* @param {number} colIndex
* @param {ConditionalColoringPlugin~ColumnOptions} columnOptions
* @example
* var colDef = {
*   "conditions": [{ // Array.<Object> Condition Object Properties
*     "expression": "[column0] > 0", // string
*     "backgroundColor": "", // Optional string e.g. #ffffff, black
*     "color": "" // Optional string e.g. #000000, white
*   }]
* };
* var colDef2 = {
*   "field": "string",
*   "colorText": true // string as Field or boolean for text coloring mode
* };
*/
ConditionalColoringPlugin.prototype.setColumnColoring = function (colIndex, columnOptions) {
	var i, grid;

	// Save column config state
	var colData = this._newColumnData(colIndex);
	if(columnOptions) {
		colData['conditions'] = columnOptions['conditions'];
		colData['colorText'] = columnOptions['colorText'];
		colData['tickColor'] = columnOptions['tickColor'];
		colData['blinking'] = columnOptions['blinking'];
	} else {
		colData['conditions'] = colData['colorText'] = colData['tickColor'] = colData['blinking'] = null;
	}

	// prepare column config
	var colorOptions = this._prepareColorOptions(colIndex, columnOptions);
	var painter = this.getColumnPainter(colIndex);
	if(colorOptions.hasColor) { // Set new painter
		if(!painter) {
			painter = colData["painter"] = new CellPainter();
		}

		if(colorOptions.condColoring) {
			painter.setConditions(colorOptions.condColoring.conditions);
		} else if(colorOptions.colorText) {
			painter.addColorTextWithTheme(colorOptions.colorText.field);
		}
		var blinkingOptions = colorOptions.blinking;
		if(blinkingOptions) {
			this._blinkingEnabled = true; // WORKAROUND
			if(blinkingOptions.customColor) {
				painter.addBlink(blinkingOptions.field, blinkingOptions.up, blinkingOptions.down, blinkingOptions.level, blinkingOptions.border);
			} else {
				painter.addBlinkWithTheme(blinkingOptions.field, blinkingOptions.border);
			}
		}
		painter["inputField"] = colorOptions.inputField;


		for(i = this._hosts.length; --i >= 0;) {
			grid = this._hosts[i];
			grid.enableColumnClass(colIndex, "conditionally-colored", true);
		}

		this._requestRowRefresh();
	} else if(painter) { // Clear existing painter
		painter.dispose(); // Clear any left timer
		this._getColumnData(colIndex)["painter"] = null;

		// Clear existing styles related to conditional coloring
		for(i = this._hosts.length; --i >= 0;) {
			grid = this._hosts[i];
			grid.enableColumnClass(colIndex, "conditionally-colored", false);
			var sect = grid.getSection("content");
			var rowCount = sect.getRowCount();
			for (var r = 0; r < rowCount; ++r) {
				var cell = sect.getCell(colIndex, r, false);
				if(cell) {
					CellPainter.clearCellStyle(cell);
				}
			}
		}
	}
};

/** @public
* @param {number} colIndex
* @param {Object=} out_obj
* @return {!Object}
*/
ConditionalColoringPlugin.prototype.getColumnColoring = function(colIndex, out_obj) {
	if(!out_obj) {
		out_obj = {};
	}
	console.log("WARNING: getColumnColoring() is not implemented yet");
	return out_obj;
};
/* @private
* @param {number} colIndex
* @param {ConditionalColoringPlugin~ColumnOptions} columnOptions
* @return {!Object}
*/
ConditionalColoringPlugin.prototype._prepareColorOptions = function(colIndex, columnOptions) {
	var colorOptions = {};
	if(!columnOptions) {
		return colorOptions;
	}
	var colField = this._getField(colIndex);
	var field = /** @type{string} */(columnOptions["field"]) || colField;
	field = ConditionalColoringPlugin._convertKeyword(field, colField);

	var conditions = columnOptions["conditions"];

	// Check if there is another type of coloring
	// "colorText" attr has more priority than "tickColor"
	var colorText = columnOptions["colorText"] || columnOptions["tickColor"];
	var inputField = columnOptions["inputField"] || "";
	var conditionCount = conditions ? conditions.length : 0;
	if(conditionCount) {
		colorOptions.condColoring = {};
		colorOptions.condColoring.conditions = conditions;
		colorOptions.hasColor = 1;
		var formatter = (field == colField) ? columnOptions["textFormatter"] : null;
		var builder = ConditionalColoringPlugin.getFilterBuilder();

		// WARNING: Builder has to have field first
		builder.setFieldDefinition(field, formatter, field);

		for(var i = conditionCount; --i >= 0;) {
			var cond = conditions[i];
			var exp = cond["expression"];
			if(!exp && field) { // Build Expression from condition
				var oper = cond["condition"];
				var value1 = cond["value1"];
				var value2 = cond["value1"];

				if(oper !== "BTW") {
					builder.addCondition(oper, value1);
				} else {
					builder.addCondition("GTE", value1);
					builder.addCondition("LTE", value2);
				}
				exp = builder.buildFilter();
				cond["expression"] = exp;
			}
		}

		colorOptions.inputField = inputField;
	} else if(colorText) {
		colorOptions.colorText = {};
		colorOptions.hasColor = 1;

		inputField = field;
		if(typeof colorText === "string") {
			inputField = colorText;
		} else if(typeof colorText === "object") {
			inputField = /** @type{string} */(colorText["field"]) || field;
		}
		colorOptions.colorText.field = ConditionalColoringPlugin._convertKeyword(inputField, field);
		colorOptions.inputField = inputField;
	}

	var blinking = columnOptions["blinking"]; // Check if there is another type of coloring
	if(blinking) {
		var blinkingOptions = colorOptions.blinking = {};
		colorOptions.hasColor = 1;

		var blinkField = field;
		if(typeof blinking === "string") {
			blinkField = blinking;
		} else if(typeof blinking === "object") {
			blinkField = /** @type{string} */(blinking["field"]) || field;
			blinkingOptions.border = blinking["border"];
			if(blinking["up"] || blinking["down"] || blinking["level"]) {
				blinkingOptions.customColor = true;
				blinkingOptions.up = blinking["up"];
				blinkingOptions.down = blinking["down"];
				blinkingOptions.level = blinking["level"];
			}
		}

		colorOptions.blinking.field = ConditionalColoringPlugin._convertKeyword(blinkField, field);
		colorOptions.blinking.inputField = blinkField;
	}

	return colorOptions;
};
/** @private
* @function
* @param {string} userInput
* @param {string} fallback
* @return {string}
*/
ConditionalColoringPlugin._convertKeyword = function(userInput, fallback) {
	if(userInput) {
		// Note that THIS_COLUMN is a special keyword that needs to be converted before usage
		return userInput.indexOf("THIS_COLUMN") ? userInput : fallback;
	}
	return userInput;
};
/** @private
* @function
* @param {Object} e
* @return {!Array.<Object>} Array of merged changes
*/
ConditionalColoringPlugin._mergeUpdates = function(e) {
	var evts = e.events; // dataSource event
	var evtCount = evts ? evts.length : 0;
	var dv = e.dataSource;
	var changedRows = [];
	for(var i = 0; i < evtCount; ++i) {
		var evt = evts[i];
		if(evt.type === "updated") {
			var rowIndex = dv.getRowIndex(evt.rid);
			var mergedChanges = changedRows[rowIndex];
			if(!mergedChanges) {
				mergedChanges = changedRows[rowIndex] = {};
			}
			var changes = evt.changes;
			for(var key in changes) {
				mergedChanges[key] = 1;
			}
		}
	}
	return changedRows;
};
/** @private
* @function
* @return {!FilterBuilder}
*/
ConditionalColoringPlugin.getFilterBuilder = function() {
	if(!ConditionalColoringPlugin._fb) {
		ConditionalColoringPlugin._fb = new FilterBuilder();
	}
	return ConditionalColoringPlugin._fb;
};
/** @type {FilterBuilder}
* @private
*/
ConditionalColoringPlugin._fb = null;

/** @private */
ConditionalColoringPlugin.prototype._requestRowRefresh = function () {
	for(var i = this._hosts.length; --i >= 0;) {
		this._hosts[i].requestRowRefresh();
	}
};
/** @public
* @param {number} colIndex
* @return {CellPainter}
*/
ConditionalColoringPlugin.prototype.getColumnPainter = function (colIndex) {
	return /** @type{CellPainter} */(this._getColumnOption(colIndex, "painter"));
};

/** Apply color to a cell base on input column index
* @public
* @param {number} colIndex Coloring condition from this column index will be used
* @param {tr.grid.Cell} cell Target cell to apply color to
* @param {Object=} rowData A row data which will be used to determine a color together with a colIndex
*/
ConditionalColoringPlugin.prototype.applyColor = function (colIndex, cell, rowData) {
	var painter = this.getColumnPainter(colIndex);
	if (!painter) { return; }

	if (!rowData) rowData = {};
	var field = this._getField(colIndex);
	var changes = {};
	changes[field] = 1;
	painter.render(cell, rowData, NaN, NaN, changes); // NaN values are min and max values for heatmap calculation
};

/** @private
* @param {Object} e
*/
ConditionalColoringPlugin.prototype._onSectionBinding = function (e) {
	var section = /** @type{tr.LayoutGrid} */(e["section"]);
	var dataRows = /** @type{Array.<Object>} */(e["dataRows"]);
	var colCount = section.getColumnCount();
	var fromR = /** @type{number} */(e["fromRowIndex"]);
	var toR = /** @type{number} */(e["toRowIndex"]);

	var c, r, cell, painter;
	if(this._realTimeGrid) {
		for (c = 0; c < colCount; ++c) {
			painter = this.getColumnPainter(c);
			if(painter) {
				for (r = fromR; r < toR; ++r) {
					if(dataRows[r]) { // prevent from null value access when using with RowGroupingExtension
						cell = section.getCell(c, r, false);
						if(cell) {
							var rowDef = /** @type{RowDefinition} */(dataRows[r]["ROW_DEF"]);
							painter.render(cell,
								rowDef.getRowData(),
								NaN, NaN,
								rowDef.getUpdates()
							); // NaN values are min and max values for heatmap calculation
						}
					}
				}
			}
		}
	} else {
		var changedRows = this._blinkingEnabled ? ConditionalColoringPlugin._mergeUpdates(e) : [];

		for (c = 0; c < colCount; ++c) {
			painter = this.getColumnPainter(c);
			if(painter) {
				for (r = fromR; r < toR; ++r) {
					cell = section.getCell(c, r, false);
					if(cell) {
						painter.render(cell,
							dataRows[r],
							NaN, NaN,
							changedRows[r]
						); // NaN values are min and max values for heatmap calculation
					}
				}
			}
		}
	}
};
/** @private
* @param {Object} e
*/
ConditionalColoringPlugin.prototype._onColumnAdded = function(e) {
	if(e.context) {
		this.setColumnColoring(e.colIndex, e.context, e.sender);
	}
};
/** @private
* @param {Object} e
*/
ConditionalColoringPlugin.prototype._onColumnRemoved = function(e) {
	var colData = /** @type{Object} */(e.columnData);
	if(!colData) { return; }

	var painter = colData["painter"];
	if(!painter) { return; }

	painter.dispose();
	colData["painter"] = null;
};


/** @public
* @function
* @param {Object.<string, string>} colors Name/Value pair object
*/
ConditionalColoringPlugin.setThemeColors = CellPainter.setThemeColors;

/** @type {Object.<string, string>}
* @private
*/
ConditionalColoringPlugin.symbolMap = {
	"EQ": "==",
	"NEQ": "!=",
	"GT": ">",
	"GTE": ">=",
	"LT": "<",
	"LTE": "<="
};


export default ConditionalColoringPlugin;
export { ConditionalColoringPlugin, ConditionalColoringPlugin as ConditionalColoring, ConditionalColoringPlugin as ConditionalColoringExtension };
