import { parseCondition } from "./Util.js";

/** @typedef {Object} FilterBuilder~Options
* @property {string} field
* @property {Function=} formatter
* @property {string=} formattedField
*/

/** FilterBuilder helps client build filter function from various types of input
* @constructor
* @param {FilterBuilder~Options=} options
*/
var FilterBuilder = function (options) {
	this._initStaticVariables();

	this._conditions = [];

	this.init(options);
};

/** @private */
FilterBuilder.prototype._initStaticVariables = function() {
	if(this._opDefs) {
		return; // The variable has been initialized. There is nothing to do here
	}

	FilterBuilder._updateTimezoneOffset();
	var timeUntilNextDay = 86400000 - (new Date() % 86400000);
	setTimeout(FilterBuilder._setUpdateInterval, timeUntilNextDay);

	var oper, opDefs;
	FilterBuilder.prototype._opDefs = this._opDefs = opDefs = {}; // Make static variable initialize only once

	opDefs["TXTEQ"] = opDefs["EQ"] = oper = {}; // Equal to. TXTEQ is equivalent to EQ the type conversion will be performed
	oper.type = "string";
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "str == input";

	opDefs["NEQ"] = oper = {}; // Not equal to
	oper.type = "string";
	oper.positive = 0;
	oper.caseSensitive = 1;
	oper.formula = "str != input";

	opDefs["GT"] = oper = {}; // Greater than
	oper.type = "number";
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "val > input";

	opDefs["GTE"] = oper = {}; // Greater than or equal to
	oper.type = "number";
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "val >= input";

	opDefs["LT"] = oper = {}; // Less than
	oper.type = "number";
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "val < input";

	opDefs["LTE"] = oper = {}; // Less than or equal to
	oper.type = "number";
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "val <= input";

	opDefs["BEGIN"] = oper = {}; // Begin with
	oper.type = "string";
	oper.positive = 1;
	oper.caseSensitive = 0;
	oper.formula = "str.indexOf(input) === 0";

	opDefs["NBEGIN"] = oper = {}; // Not begin with
	oper.type = "string";
	oper.positive = 0;
	oper.caseSensitive = 0;
	oper.formula = "str.indexOf(input) !== 0";

	opDefs["END"] = oper = {}; // End with
	oper.type = "string";
	oper.positive = 1;
	oper.caseSensitive = 0;
	oper.formula = "(str.length >= input.length) && str.lastIndexOf(input) === (str.length - input.length)";

	opDefs["NEND"] = oper = {}; // Not end with
	oper.type = "string";
	oper.positive = 0;
	oper.caseSensitive = 0;
	oper.formula = "(str.length < input.length) || str.lastIndexOf(input) !== (str.length - input.length)";

	opDefs["CONT"] = oper = {}; // Contain
	oper.type = "string";
	oper.positive = 1;
	oper.caseSensitive = 0;
	oper.formula = "str.indexOf(input) >= 0";

	opDefs["NCONT"] = oper = {}; // Not contain
	oper.type = "string";
	oper.positive = 0;
	oper.caseSensitive = 0;
	oper.formula = "str.indexOf(input) < 0";

	opDefs["BLANK"] = opDefs["EQ_BLANK"] = oper = {}; // Equal to blank value
	oper.type = "blank"; // Blank checking
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "!val && val !== 0"; // false boolean value would not pass

	opDefs["NBLANK"] = opDefs["EQ_NBLANK"] = oper = {}; // Equal to non blank value
	oper.type = "blank"; // Blank checking
	oper.positive = 1;
	oper.caseSensitive = 1;
	oper.formula = "(val || val === 0) ? true : false";
};
/** @type {Object.<string, Object>}
* @private
*/
FilterBuilder.prototype._opDefs = null; // Static variable. Lazy init.

/** @type {string}
* @private
*/
FilterBuilder.prototype._fieldName = "";
/** @type {string}
* @private
*/
FilterBuilder.prototype._formattedFieldName = "";
/** @type {Function}
* @private
*/
FilterBuilder.prototype._formatter = null;
/** @type {Array.<Object>}
* @private
*/
FilterBuilder.prototype._conditions = null;


/** @type {number}
* @private
*/
FilterBuilder._timezoneOffset = 0; // in milliseconds
/** @private
* @function
*/
FilterBuilder._updateTimezoneOffset = function() {
	FilterBuilder._timezoneOffset = (new Date()).getTimezoneOffset() * 60;
};
/** @private
* @function
*/
FilterBuilder._setUpdateInterval = function() {
	FilterBuilder._updateTimezoneOffset();
	setInterval(FilterBuilder._updateTimezoneOffset, 86400000); // 24 hours
};


/** @public */
FilterBuilder.prototype.reset = function() {
	this.setFieldDefinition("");
};
/** @public
* @param {FilterBuilder~Options=} options
*/
FilterBuilder.prototype.init = function(options) {
	if(!options) {
		return;
	}
	var field = options["field"];
	if(field) {
		this.setFieldDefinition(field, options["formatter"], options["formattedField"]);
	}
};

/** @public
* @param {string} fieldName
* @param {Function=} formatter
* @param {string=} formattedField
*/
FilterBuilder.prototype.setFieldDefinition = function(fieldName, formatter, formattedField) {
	this._fieldName = fieldName;
	this._formattedFieldName = (formattedField) ? formattedField : fieldName + "_FORMATTED";
	this._formatter = formatter || null;
	this._conditions.length = 0;
};
/** @public
* @param {string} oper
* @param {string|number} value
* @param {string=} opt_connector
*/
FilterBuilder.prototype.addCondition = function (oper, value, opt_connector) {
	oper = oper ? oper.toUpperCase() : "";
	var opDef = this._opDefs[oper];
	if (!opDef) {
		return; // Unknown operation
	}

	if (value === "{}" || value === "{*}") { // ignore operator if the value is BLANK or NON-BLANK
		oper = (opDef.positive ^ value === "{}") ? "EQ_NBLANK" : "EQ_BLANK";
	} else if (!value && value !== 0) { // TODO: Support false boolean value
		return; // Invalid comparer value
	}

	opt_connector =  (opt_connector && opt_connector.toUpperCase() === "OR") ? "||" : "&&";

	var obj = {};
	obj.oper = oper;
	obj.value = value;
	obj.connector = opt_connector;
	obj.definition = this._opDefs[oper];

	this._conditions.push(obj);
};
/** @public
* @return {Function}
*/
FilterBuilder.prototype.buildFilter = function() {
	if (!this._fieldName) { return null; }
	var len = this._conditions.length;
	if(!len) { return null; }

	var i, opDef;
	var hasTextOp = 0; // EQ, NEQ, BEGIN, NBEGIN, END, NEND, CONT, NCONT
	var hasCaseInsensitiveOp = 0; // BEGIN, NBEGIN, END, NEND, CONT, NCONT
	var hasNumberOp = 0; // GT, GTE, LT, LTE
	// var hasBlankOp = 0; // EQ_BLANK, EQ_NBLANK

	// Collect filter info
	for (i = 0; i < len; i++) {
		opDef = this._conditions[i].definition;
		if(opDef.type === "string") {
			++hasTextOp;
		}
		if(opDef.type === "number") {
			++hasNumberOp;
		}
		if(!opDef.caseSensitive) {
			++hasCaseInsensitiveOp;
		}
	}
	var fieldName = this._fieldName;
	var fmtFieldName = this._formattedFieldName;

	var contextObj = null;
	var filterStr = "var val = rowData['" + FilterBuilder._escapeQuotes(fieldName) + "'];\n";

	if(hasTextOp || hasNumberOp) { // For mixed data type column
		filterStr += "if(!val && val !== 0) { val = NaN; }\n";
		if(hasTextOp) {
			if(this._formatter) {
				contextObj = {};
				contextObj["format"] = this._formatter;
				filterStr += "var str = this.format(rowData);\n";
			} else {
				if(fmtFieldName !== fieldName) {
					filterStr += "var str = rowData['" +
						FilterBuilder._escapeQuotes(fmtFieldName) +
						"'];\n";
					filterStr += "if(!str && str !== 0) {" +
						"str = (!val && val !== 0) ? '' : val + ''; }\n";
				} else {
					filterStr += "var str = (!val && val !== 0) ? '' : val + '';\n";
				}
			}

			if(hasCaseInsensitiveOp) {
				filterStr += "if(str) { str = str.toLowerCase(); };\n";
			}
		}
		if (hasNumberOp && fieldName.indexOf("FREE_TEXT") !== -1) {
			filterStr += "val = !isNaN(Number(val)) ? Number(val) : val;\n";
		}
	}

	var condStr = "";
	for (i = 0; i < len; i++) {
		if (i > 0) {
			condStr += " " + this._conditions[i - 1].connector + " ";
		}

		var cond = this._conditions[i];
		opDef = cond.definition;
		if(opDef.type === "blank") { // Blank checking operation (EQ_BLANK, and EQ_NBLANK) requires no data transformation
			condStr += "(" + cond.definition.formula + ")";
			continue;
		}


		var inputVal = cond.value;
		if(opDef.type === "string") {
			if(this._formatter && typeof inputVal !== "string") {
				var dummyRow = {};
				dummyRow[fieldName] = inputVal;
				dummyRow[fmtFieldName] = inputVal;
				inputVal = this._formatter(dummyRow); // WARNING: dummyRow may not provide enough data for formatting correctly
			} else {
				inputVal = FilterBuilder._toText(inputVal);
			}

			if(!opDef.caseSensitive && inputVal) {
				inputVal = inputVal.toLowerCase();
			}
		} else if(!inputVal && inputVal !== 0) { // TODO: Support of false value
			inputVal = NaN;
		} else if(opDef.type === "number" ) {
			inputVal = parseFloat(inputVal);
		}

		if (typeof inputVal === "string") { // inputVal can still be a number at this point;
			inputVal = "'" + FilterBuilder._escapeQuotes(inputVal) + "'";
		}
		condStr += "(" + cond.definition.formula.replace(/input/g, inputVal) + ")";
	}
	filterStr += "return " + condStr + ";";

	var func = null;
	try {
		func = /** @type{Function} */ (eval("(function(rowData) {\n" + filterStr + "\n})"));
		if(contextObj) {
			func = func.bind(contextObj);
		}
	} catch(err) {} // eslint-disable-line no-empty

	// Release all resources after building the filter whether success or not
	this._conditions.length = 0;
	return func;
};
/** @public
* @param {string} filterStr e.g. GT100ANDLTE200
* @param {string} field
* @param {Function=} formatter
* @param {string=} formattedField
* @return {Function}
*/
FilterBuilder.prototype.buildMonitorFilter = function(filterStr, field, formatter, formattedField) {
	this.setFieldDefinition(field, formatter, formattedField);
	var needConvertValue = (field.lastIndexOf("_LOCAL") === field.length - 6); // _LOCAL field is special monitor field which convert time value to local timezone (idn)

	var conds = filterStr.split("");
	for (var i = 0; i < conds.length; i++) {
		var tokens = conds[i].split("");
		if(needConvertValue) {
			tokens[1] = ((tokens[1] - FilterBuilder._timezoneOffset) % 86400) + ""; // Convert to local timezone value for range filtering
		}
		this.addCondition(tokens[0], tokens[1], tokens[2]);
	}

	return this.buildFilter();
};

/** @public
* @param {*} condition This can be string, array, or function
* @param {string} field
* @param {Function} formatter
* @param {string} formattedField
* @return {Function}
*/
FilterBuilder.prototype.parse = function(condition, field, formatter, formattedField) {
	var type = typeof(condition);
	if(type === "string") {
		if(condition.indexOf("") >= 0) {
			return this.buildMonitorFilter(condition, field, formatter, formattedField);
		} else {
			return parseCondition(condition);
		}
	} else if(Array.isArray(condition)) {
		this.setFieldDefinition(field, formatter, formattedField);
		var len = condition.length;
		for(var i = 0; i < len; ++i) {
			var subCond = condition[i];
			this.addCondition(subCond[0], subCond[1], subCond[2]);
		}
		return this.buildFilter();
	} else if(type === "function") {
		return condition;
	}
	return null;
};

/** @private
* @function
* @param {*} val
* @return {string}
*/
FilterBuilder._toText = function(val) {
	if(typeof val == "string") {
		return val;
	}
	if(!val && val !== 0) {
		return ""; // null, undefined, NaN, and false
	}
	return val + "";
};

/** @private
* @function
* @param {string} str
* @return {string}
*/
FilterBuilder._escapeQuotes = function(str) {
	return (str + '').replace(/[\\"']/g, '\\$&').replace(/\u0000/g, '\\0'); // eslint-disable-line no-control-regex
};

export default FilterBuilder;
export {FilterBuilder};
